    export default {
        name: 'neil-modal',
        props: {
            title: { //
                type: String,
                default: ''
            },
            content: String, //
            align: { //content left/center/right
                type: String,
                default: 'left'
            },
            cancelText: { //""
                type: String,
                default: ''
            },
            cancelColor: { //
                type: String,
                default: '#333333'
            },
            confirmText: { //""
                type: String,
                default: ''
            },
            confirmColor: { //
                type: String,
                default: '#007aff'
            },
            showCancel: { // true
                type: [Boolean, String],
                default: true
            },
            show: { //
                type: [Boolean, String],
                default: false
            },
            autoClose: { //
                type: [Boolean, String],
                default: true
            }
        },
        data() {
            return {
                isOpen: false
            }
        },
        watch: {
            show(val) {
				this.isOpen = val
            }
        },
        created() {
        	this.isOpen = this.show
        },
        methods: {
            bindTouchmove() {},
            clickLeft() {
                setTimeout(() => {
                	this.$emit('cancel')
                }, 200)
                this.closeModal()
            },
            clickRight() {
                setTimeout(() => {
                	this.$emit('confirm')
                }, 200)
                this.closeModal()
            },
			clickMask(){
				if(this.autoClose){
					this.closeModal()
				}
			},
            closeModal() {
                this.showAnimation = false
				this.isOpen = false
				this.$emit('close')
            }
        }
    }
