	import Vue from 'vue';
	Vue.__xfl_select = Vue.__xfl_select || new Vue();  // xfl-select
	export default {
		name: 'xfl-select',
		props: {
			list: {            // 
			  type: Array,  
			  default: function(){
				  return [];
			  }
			},
			focusShowList: null, // input
			initValue: null,   // 
			isCanInput: {      // 
			  type: Boolean,  
			  default: false,
			},
			selectHideType: {     // 
			  type: String,        
			  default: 'hideAll', // 'independent' -   'hideAll' - 
								  // 'hideOthers'-    
								  // 'hideSelf' -  
			},
			placeholder: {     // placeholder
			  type: String,  
			  default: '',
			},
			style_Container: { // 
			  type: String,  
			  default: ''
			},
			disabled: {        // 
			  type: Boolean,  
			  default: false,
			},
			showItemNum: {     // 
			  type: Number,  
			  default: 5
			},
			listShow: {        // 
			  type: Boolean,  
			  default: false
			},
			clearable: {       // 
			  type: Boolean,  
			  default: true
			},
		},
		data() {
			return {
				isShowList: false, // 
				selectText: '',    // 
				activeIndex: -1,   // 
				isRotate: false,   // 
				listTop__: 50,       // topinput
			};
		},
		// 
		watch: { //  mounted
			listShow(newVal, oldVal){
				this.onDataChange_listShow(newVal, oldVal);
			},
		},
		computed:{
			focusShowList__(){ // 
				if(this.focusShowList == null ){
					//  pc




						return false;

				}else{
					return 	this.focusShowList;
				}
			},
			listBoxHeight__(){ // 
				const itemHeight = 2; // (em), 2
				return this.showItemNum*itemHeight;
			},
			showInput(){    // 
				return this.isCanInput && !this.disabled;
			},
			innerList(){    // 
				const arr = [], orginArr = this.list;
				orginArr.forEach((val, index)=>{
					let value = typeof val === 'object' && 'value' in val ? val.value : val;
					let isDisabled = typeof val === 'object' && val.disabled == true;
					arr.push({
						isActive: false,
						value: value,
						disabled: isDisabled,
					});
				});
				return arr;
			},
		},
		mounted(){
			Vue.__xfl_select.$on('open', this.onOtherXflSelectOpen);
			this.switchMgr = new Switch(this.onListShow, this.onListHide);  // 
			this.onDataChange_listShow(this.listShow, null); //  watch 
			this.init(); //
		},
		beforeDestroy(){
			Vue.__xfl_select.$off('open', this.onOtherXflSelectOpen);
		},
		methods: {
			onOtherXflSelectOpen(component){ //
				if(this.selectHideType === 'independent' || this.selectHideType === 'hideOthers'){
					return;
				}
				component !== this && this.switchMgr.close(100);
			},
			/**************************  ****************************/
			//
			init(){
				this.clearInput();  // placeholder
				this.setInput(this.initValue); // 
				this.changeActiveIndex(this.initValue); // 
				this.getInputBoxHeight(); // top
			},
			
			//  px
			getInputBoxHeight(){
				let component = this;



				getNodeInfo('.show-box', component, (data)=>{
					if(data){
						const trangleHeight = 6; //(px)
						this.listTop__ = data[0].height + trangleHeight;
					}
				})
			},
			/**************************  ****************************/

			/**************************  ****************************/
			getIndex(value){  // 
				let activeIndex = searchIndex(
									this.innerList, value, 'value')
				return activeIndex;  // -1
			},
			itemIsDisabled(index){ // 
				return this.innerList[index].disabled;
			},

			itemIsActive(index){ // ()
				return index === this.activeIndex;
			},

			// listShow 
			onDataChange_listShow(newVal = false, oldVal){ 
				newVal ? this.switchMgr.open() : this.switchMgr.close(100);
			},
			/**************************  ****************************/
			
			
			/**************************  ****************************/
			// 
			onFocus(event){
				this.focusShowList__ && this.switchMgr.open();
				this.$emit('focus', event);
			},
			
			// 
			onBlur(event){
				// 
				this.focusShowList__ && this.switchMgr.close(100); 
				this.$emit('blur', event);
			},
			
			//
			onUpperClick(){
				if(this.disabled){
					return;
				}
				this.switchMgr.toggle('auto', -1, 100);
				this.$emit('input-click');
			},
			
			//
			onClear(){ 
				this.clearItemActive(); // 
				this.clearInput(); // 
				this.$emit('clear');
			},
			
			// 
			onInput(event){
				const inputVal = event.detail.value;
				this.changeActiveIndex(inputVal);
				this.$emit('input', event);
			},

			// input
			clearInput(placeholder = null){
				this.placeholder = placeholder== null ? this.placeholder : placeholder;
				this.selectText = this.showInput ? '' : this.placeholder;
			},
			// input
			setInput(text = null){ 
				if(text == null){
					return;
				}
				this.selectText = text;
			},
			/**************************  ****************************/
			
			
			/************************** (//) ****************************/

			/**
			 * value, 
			 *  
			 * 1.   
			 * 2.   
			 */
			changeActiveIndex(value_index){ //
				if(value_index == null){
					return;
				}
				let activeIndex = value_index, value = value_index;
				if(typeof value_index !== 'number'){ //
					activeIndex = this.getIndex(value); // 
				}else{
					value = this.innerList[activeIndex].value;
				}
				if(activeIndex > -1){
					!this.itemIsActive(activeIndex) && this.setItemActive(activeIndex, value);
				}else{
					this.clearItemActive();
				}
				this.setInput(value);  // 
			},

			clearItemActive(index = -1){  // 
				if(index < 0){ // 
					this.activeIndex = -1;
				}
			},
			setItemActive(index, value){ //
			    if(this.itemIsDisabled(index)){
					return;
				}
				this.activeIndex = index;
			},

			// 
			onListClick(){

			},
			onClickItem(index, value){  // 
				if( this.itemIsDisabled(index) ){
					this.switchMgr.open(); // 
					return;
				}
				this.switchMgr.close(100);   // 
				if(this.disabled){ //  
					return;
				}
				if( !this.itemIsActive(index) ){  //
				    this.clearItemActive(); // 
					this.setItemActive(index, value); // 
					this.$emit('change', {newVal: value, oldVal: this.selectText, 
											index: index, orignItem: this.list[index]});
											
					this.setInput(value);   // 
				}
			},
			onListHide(){ //
				this.isRotate = false;
				this.isShowList = false;
				this.$emit('visible-change', false);
			},
			onListShow(){  //
				this.isShowList = true;
				this.isRotate = true;
				this.$emit('visible-change', true);
				
				if(this.selectHideType === 'independent' || this.selectHideType === 'hideSelf'){
					return;
				}
				Vue.__xfl_select.$emit('open', this);
			}
			/************************** (//) ****************************/
		}
	}

