	/**
	 * web
	 * @public
	 * @returns {boolean} truewebfalsewebpc
	 */
	function isMobile(){
		try{  // window
			let reg = /iPhone|iPad|iPod|iOS|Android|SymbianOS|Windows Phone|coolpad|mmp|smartphone|midp|wap|xoom|symbian|j2me|blackberry|wince/i;
			return reg.test(navigator.userAgent); 
		}catch(e){
			return false;
		}
	}
	/**
	 * webpc
	 * @public
	 * @returns {boolean} truewebpcfalseweb
	 */
	function isPC(){
		try{  // window
			let reg = /iPhone|iPad|iPod|iOS|Android|SymbianOS|Windows Phone|coolpad|mmp|smartphone|midp|wap|xoom|symbian|j2me|blackberry|wince/i;
			return !reg.test(navigator.userAgent); 
		}catch(e){
			return false;
		}
	}
	/**
	 * 
	 * :  
	 * 1.   import Vue from 'vue'  
	 * 2. , component(h5wx)
	 * @public
	 * @param {Object|string} options - selector
	 *                         selector - dom: 
	 * 							1. ID'#the-id'
								2. class'.a-class.another-class'
								3. '.the-parent > .the-child'
								4. '.the-ancestor .the-descendant'
								5. '.the-ancestor >>> .the-descendant'
								6. '#a-node, .some-other-nodes'
								7.  'viewport' window
	 * @param {function|component} [callback=null] - , 
	            :   
				// ,  
				// options  
				{  
					id: '',         // String  ID, id()  
					dataset: null,  // Object  dataset  
					left: 0,        // Number (px)(page)  
					right: 0,       // Number (px)  
					top: 0,         // Number (px)  
					bottom: 0,      // Number (px)  
					width: 0,       // Number (px)  
					height: 0,      // Number (px)  
					scrollLeft: 0,  // Number (px)  
					scrollTop: 0,   // Number (px)  
					context: {} || null,   // ObjectContext(VideoContextCanvasContextMapContext)  
					...        // properties computedStyle  
				}  
				// null  
	 * @param {any} [thisObj=null] this, 
	 * @return {undefined|promise} callbackpromiseundefined  
	 * @example
	 * 1. promise
	 * getNodeInfo('#aa').then((data)=>{ console.log(data);});
	 * 
	 * 2. component, promise
	 * getNodeInfo('#aa', this).then((data)=>{ console.log(data);});
	 * 
	 * 3. callback, undefined
	 * getNodeInfo('#aa', (data)=>{ console.log(data);});
	 * 
	 * 4. callback, undefined
	 * getNodeInfo({selector: '#aa', component: this}, (data)=>{ console.log(data);});
	 */
	function getNodeInfo({
		selector = 'selector', // 
		component = null, // 
		attemptSpaceTime = 16,  // (ms): 16 24 36 54 81 122 183 275 413 
		attemptSpaceRate = 1.5, // 
		totalAttemptNum = 8,    // 
		// 
		id = true,        // Boolean	 id	
        dataset = true,   // Boolean	 dataset	
        rect = true,      // Boolean	left right top bottom	
        size = true,      // Boolean width height	
        scrollOffset = true, //Boolean  scrollLeft scrollTop
        //   App 
        properties = [],  // Arraystring    
                          // 
                          // id class style 
        computedStyle = [],  //Arraystring
        context = true,  // Boolean  Context 	
	} = {}, callback = null, thisObj = null){
		// arguments 
		// 
		const args = arguments;
		selector = typeof args[0] === 'string' ? args[0] : String(selector);
		if(typeof args[1] !== 'function'){
			component = args[1]; callback = args[2]; thisObj = args[3];
		}
		!component instanceof Vue && (component = null);  //
		
		//  component wx
		const options = { selector, attemptSpaceTime, totalAttemptNum, attemptSpaceRate,
		id, dataset, rect, size, scrollOffset, properties, computedStyle, context };
		
		const selectorQuery = uni.createSelectorQuery();
		component && selectorQuery.in(component);
		const nodesRef = selector === 'viewport' ? selectorQuery.selectViewport() : selectorQuery.selectAll(selector);
		nodesRef.fields(options); // 

		let result; // promiseh5exec
		if(typeof callback !== 'function'){
			result =  new Promise(resolve=>callback = resolve);
		}
		stepRunFunc((next, currNum)=>{
			selectorQuery.exec( ([data]) => { // 
				data && data.length === 0 && (data = null);
				data || totalAttemptNum <= currNum ? typeof callback === 'function' && callback.call(thisObj, data) : next(attemptSpaceTime);
				attemptSpaceTime = Math.round( attemptSpaceTime * attemptSpaceRate );
			});
		})(); // 
		
		return result;
	}
